
import { useState, useEffect, useRef } from 'react';

function Pop ({name}){
    let [u,su] = useState("none")

    function b(){
        setTimeout(() => {
            su("block")
            setTimeout(()=>{
                su("none")
            },3000)
            
        }, 2000);
    }
    useEffect(()=>{
b()
    },[])


    return(
        <div >
            <div className="Pop" style={{display:u}}>
              <button onClick={e=>{su("none")}}>a</button>
              {name}
            </div>
        </div>
    )



}

export default Pop;