const PrivateRoute = ({ component: Component, ...rest }) => {
  const location = useLocation();
  let dat = localStorage.getItem("token")
  let j;
  if (dat === null || dat === undefined) {
    j = false
  } else {
    const response = fetch(` https://rougele.herokuapp.com/auth/`, {
      method: "GET",
      headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${dat}`, },
    })
    if (response.status === 403) {
     
      j = false
    } else {
      j = true
    }
  }

  // let a = await response.json()


  return (
    <Route {...rest}>{j === true ?
      <div>
      <Component />  
      <SideBar/>
      <Searchi />
      </div>
      
      :
      <Redirect to={{ pathname: "/login", state: { from: location } }} />
    }
    </Route>
  );
};


