import { useState, useEffect, useRef } from 'react';
//  eslint-disable-next-line
import { BrowserRouter, Link, useLocation } from "react-router-dom";
//  eslint-disable-next-line
import Profile from './page'

function useQuery() {
  return new URLSearchParams(useLocation().search);
}



function Search() {
  const ref = useRef(null);
  const reff = useRef(null);
  let query = useQuery();
  let [data, setdata] = useState([])
  //  eslint-disable-next-line
  let [da, ta] = useState(false)
  const [Posts, setPosts] = useState([]);
  let token = localStorage.getItem("token")
  let tok = localStorage.getItem("name")
  let [u, su] = useState("none")
  let [newe,snewe] = useState("")

  let a = []
  let b = 0;
  let ro = 0;

  console.log(query.get("query"))
  let get = async () => {
    let data = {
      data: query.get("query"),
      person: tok
    }
    let res = await fetch(" https://rougele.herokuapp.com/search-users", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', },
      body: JSON.stringify(data)
    })

    let k = await res.json()
    setdata(k)

    let ress = await fetch(" https://rougele.herokuapp.com/search-posts", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', },
      body: JSON.stringify(data)
    })
    let kk = await ress.json();
    setPosts(kk)
    // Posts[0].likes = 10
    console.log(Posts[0])


  }

  let Like = async (e) => {
    let data = {
      id: e
    }
    let res = await fetch(" https://rougele.herokuapp.com/like-post", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    let h = await res.json()

    if (h.liked) {
      document.getElementById(`${e}`).innerHTML = `  <div class = "flame"></div>${h.a}`

    } else {
      document.getElementById(`${e}`).innerHTML = `  <div class = "lame"></div>${h.a}`
    }

  }

  // function ba() {
  //   setTimeout(() => {
  //     su("block")
  //     setTimeout(() => {
  //       su("none")
  //     }, 3000)

  //   }, 2000);
  // }
  let refresh = async (d, e) => {

    // reff.current.innerHTML = reff.current.innerHTML + 1
    //  reff.current.innerText = "aaa"
    // reff.current.children.palcount++



    console.log()
    let re = await fetch(" https://rougele.herokuapp.com/post-likes", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify({ postid: d })
    })
    let a = await re.json()
    if (a.liked) {
      document.getElementById("likebtn").innerHTML = `<div id="likebtn1">liked</div><div id="likebtn2">${a.a}</div>`
    } else {
      document.getElementById("likebtn").innerHTML = `<div id="likebtn1">like</div><div id="likebtn2">${a.a}</div>`
    }
    // e.target.nextSibling.innerText  = a.a
    // e.target.nextSibling.id = ""
    // console.log(e.target.nextSibling.id)
    // ba()




  }


  const scroll = (scrollOffset) => {
    ref.current.scrollLeft += scrollOffset;
  };

  function bB() {
    // reff.current.innerText = "aaa"
    console.log(reff.current.children)

  }
  function bike(a) {
    console.log(a)
  }

  async function book(a,e){
  
    let res = await fetch(" https://rougele.herokuapp.com/bookmark", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(a)
    })
    let ga = await res.json()
    if(ga.delete){
      console.log("mello")
      su("block");
      setTimeout(() => {
          su("none")
      
          }, 2000);
          // console.log( document.getElementsByClassName(`${e}book`));

          document.getElementsByClassName(`${e}book`).innerText = `Bookmark`


      snewe("Post Has Been Deleted from Bookmarks")
    }else{
      su("block");
      setTimeout(() => {
          su("none")
      
          }, 2000);

      snewe("Post Has Been Added to  Bookmarks")
    }
  }
  async function kilt(n){
    
    let res = await fetch(` https://rougele.herokuapp.com/${n}/photo`, {
      method: "GET",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
  
    })
    let h = await res.json()
    console.log(h)
    let gi = `https://matthewkwap.github.io/pic/${h.a}.jpg`
   
    return gi
    
  }
  useEffect(() => {
    get()
  }, [])
  let sta = {
    color: "blue",
    content: "hello"
  }
  return (
    <div>
      {/* <Child name={query.get("query")} /> */}

      <div id="s-p">

        <button id="left" onClick={() => scroll(-300)}> L </button>
        <button id="right" onClick={() => scroll(300)}>R</button>
        <div ref={ref} id="proff">


          <div id="prof">

            {data.map(r => {
              return (
                <div key={r.userid} >
                  <a id="view" href={`/profile/${r.username}`}>View profile</a>
                  <img src={`https://matthewkwap.github.io/pic/${r.imageid}.jpg`} loading="lazy" id="p-img4" />
                  <h2 id="p-n">{r.name}</h2>
                  <a id="p-name" > @{r.username}</a>

                  {/* {r.following?<button id="p-foll">unfollow</button>:<button id="p-foll">follow</button>} */}
                  {/* <div id="p-bio">
                  <p id="p-bin">{r.bio}</p>
                </div> */}
                </div>
              )
            })}
          </div>


        </div>
      </div>




      <div className="pa-posts">
        {Posts.map(e => {
          return (
            <div key={e.postid} ref={reff} >

              <div className="pa-post">
                <div id="cs-12">
                  <p id="data">{e.data}</p>
                </div>
                <div className="cs-un">
                  <div id="cs-un-1"><img src={`https://matthewkwap.github.io/pic/${e.img}.jpg`} id="img4" /></div>
                  <div id="cs-un-2"><a href={`/profile/${e.name}`} id="post-owner">@{e.name}</a></div>

                  <div id="cs-un-3"><h1 id="date">Created {e.datecreated}</h1></div>
                </div>
                <div class="dropdown">
                  <div class="dropbtn">:</div>
                  <div class="dropdown-content">
                    {e.book?
                    <button href="#" id="drop-del" className={`${e.postid}book`} onClick={a => { book(e,e.postid) }}> Remove from Bookmark</button>
                    :
                    <button href="#" id="drop-bok" className={`${e.postid}book`} onClick={a => { book(e,e.postid) }}>Bookmark</button>
                    }
                   

                    {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hello</button>} */}

                  </div>
                </div>

                <div >
                  <div className="flex-container" >
                    {e.liked ?
                      <div>
                        <button className={`likebtn`} id={`${e.postid}`} onClick={a => { Like(e.postid); }}>
                          <div class="flame"></div>
                        </button>
                      </div>

                      :
                      <div>
                        <button className={`likebtn`} id={`${e.postid}`} onClick={a => { Like(e.postid); }}>
                          <div id="blame">
                            <div class="lame"></div>
                          </div>

                        </button>
                      </div>

                    }

                    <Link to={`/u/${e.name}/${e.postid}`} id="c-btn">🎫Comment</Link>




                  </div>
                </div>

              </div>




            </div>



          )

        })}

      </div>






      <div >
        <div className="Pop" style={{ display: u }}>
          <button id="dip" onClick={e => { su("none") }}>x</button>
          <label id="dip2">{newe}</label>
        </div>
      </div>


    </div>
  )
}

export default Search;