import { Link, } from "react-router-dom";
import { useEffect,useState} from 'react'

function SideBar() {
    let [name,setname] = useState("");
    let [img,setimg] = useState("")
    let [un,sun] = useState("")
    let [g,sg] = useState()
    let dat = localStorage.getItem("token")

    let get = async () => {
        const res = await fetch(` https://rougele.herokuapp.com/admin-data/`, {
            method: "GET",
            headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${dat}`, },
        })
        let j = await res.json()
        let name = localStorage.getItem("name")
        let rond = localStorage.getItem("usn")
        let ja = localStorage.setItem("imga",j.img)
        setimg(j.img)

        
        setname(rond)
        sun(name)
    }
    let out = () =>{
        localStorage.clear()
    }
    useEffect(()=>{
        get()
    },[])
    
    


    return (
        <div>
            <div className="Side-bar">
                <div className="user-info">
                    <div id='img1'>
                    <img src={`https://matthewkwap.github.io/pic/${img}.jpg`} id="img2" />
                    

                    </div>
                    <h1 id="u-i-n">{name}</h1>
                    <Link to={`/profile/${un}`} id="u-i-un">@{un}</Link>

                </div>
                <div className="side-items">
                    <Link to="/home" id="side-item">Home</Link>
                    <Link to={`/profile/${un}`} id="side-item">Profile</Link>
                    <button id="side-item" onClick={out}> Log Out</button>
                    {/* <Link to="/bookmark" id="side-item">Bookmark</Link>
                    <Link to="/messages" id="side-item">Messages</Link> */}
                </div>
            </div>
        </div>
    )
}

export default SideBar