import { Link , Route, Switch } from "react-router-dom";
import { useEffect,useState,useContext,createContext} from 'react'
import Test from './message'
import { useParams, useRouteMatch } from "react-router-dom";


export const m = createContext(null)

function SideBa() {
    // let m = useContext([])
 
    let [users,setusers] = useState([])
    let [pers,setpers] = useState([])
    let [name,setname] = useState("");
    let [img,setimg] = useState("")
    let [un,sun] = useState("")
    let [chat,schat] = useState([])



    let a = `{
        "squadName" : "Super Hero Squad",
        "homeTown" : "Metro City",
        "formed" : 2016,
        "secretBase" : "Super tower",
        "active" : true,
        "members" : [
         
          {
            "name" :{
              "post1":"helloo",
              "post2":"hello",
              "post3":"hello"
              
      
            }
            
            
          }
          
        ]
      }`
console.log(JSON.parse(a))

      
 
    let dat = localStorage.getItem("token")
    let ont = localStorage.getItem("name")
   
    let rond = localStorage.getItem("usn")



    const { id } = useParams();
    let [load,nload] = useState(false)

    let [user,setuser] = useState("") 
    let get = async () => {
        const res = await fetch(` https://rougele.herokuapp.com/user-messages/`, {
            method: "GET",
            headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${dat}`, },
          
        })
        let j = await res.json()
        setusers(j)

        console.log(j)
    
    }
    let bet = async(a) =>{
        const res = await fetch(` https://rougele.herokuapp.com/get-messages/`, {
          method: "POST",
          headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${dat} `, },
          body:JSON.stringify({id:a})
        })
        let r = await res.json()
        localStorage.setItem("chat",JSON.stringify(r))
        
      schat(r)
        // console.log(r)
      
      }

    let gat = async () => {
        let name = localStorage.getItem("name")
        let rond = localStorage.getItem("usn")

      
        setname(rond)
        sun(name)

    }

    function setUse(e,a){
        console.log(e)

    
        setuser(e)
        setpers(a)
        localStorage.setItem("messen",e)
        bet(e)
        if(!load){
           nload(true) 
        }else{
          nload(false) 
        setTimeout(() => {
          nload(true) 
          
        }, 1000);
}
  
       

        
    }
    let delet = async(id,name) =>{
        let r = {
            id:id,
            username:name
        }

        const res = await fetch(`http://localhost:3001/delete-message/`, {
          method: "POST",
          headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${dat} `, },
          body:JSON.stringify(r)
        })

    }
    useEffect(()=>{
  

        get()
        gat()
    },[])
    
    


    return (
        <div>
            {/* ✉📩✉📨📧🎫 */}
           
            {load?
            <m.Provider value={chat}>
                <Test users={user} naem={pers} />
            </m.Provider>
            :
            <div className="m-name">you dont have a message selected</div>
            }
            
            <div className="m-Side-bar">
               
                 
                {/* <div className="m-user-info">
                    <div id='m-img1'>
                    <img src={`http://localhost:3001/${un}/photo`} id="m-img2" />

                    </div>
                    <h1 id="m-u-i-n">{name}</h1>
                    <Link to={`/profile/${un}`} id="m-u-i-un">@{un}</Link>

                </div> */}
                <div className="k-side">
                    <div className="mes-t">
                       <label id="mes-u">Messages</label> 
                    </div>
                     
                      <div className="m-side-items">
                   
                   
                    {users.map(e=>(
                        <button to={`/m/${e.id}`} onClick={a=>{setUse(e.id,e.name); }} key={`${e.name}`} id="m-side-item">
                        <img src={`http://localhost:3001/${e.name}/photo`} id="ma-img"/>
                        <label id="m-lab">{e.name}</label>
                      <button  id="m-del" onClick={a=> {delet(e.id,e.name)}}>aaaa</button>
                        
                    </button>
                      
                    
                         
                    
                ))}
                    {/* <Link to="/home" id="m-side-item">Hatice</Link>
                    <Link to={`/profile/`} id="m-side-item">Profile</Link>
                    <Link to="/bookmark" id="m-side-item">Bookmark</Link>
                    <Link to="/messages" id="m-side-item">Messages</Link> */}
                </div>   
                </div>
           
            </div>
        </div>
    )
}

export default SideBa