import { Link  } from "react-router-dom";
import { useEffect, useState } from "react";
    //  eslint-disable-next-line
import Notfound from './notfound'
import Loading from './loading'
// import Add from './Add'

function Profile(prop) {


  let r = new Date()
  console.log(r.toLocaleString('en-us'))

  console.log(Date.parse(r.toLocaleString('en-us')))


  const id = "meice"
  let [load, sload] = useState(false)
  // let [name, setname] = useState("");
  // let [img, setimg] = useState("")
  let [postd, spostd] = useState("")
  // let [un, sun] = useState("")
  let [bord, sbord] = useState()
  // let ga = true
  let [likes, setlikes] = useState([]);
  let token = localStorage.getItem("token")
  let lid = localStorage.getItem("name")
  // const liked = "blue"
  console.log(id)
  const [Posts, setPosts] = useState([]);

  let posts = async () => {
    // let dat = localStorage.getItem("token")

    const res = await fetch(` https://rougele.herokuapp.com/home-posts/`, {
      method: "GET",
      headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${token}` },
    })


    let j = await res.json()
   

   
      setPosts(j)
     

      sload(true)


    

  }


 
  


  
  async function book(a){
    let res = await fetch(" https://rougele.herokuapp.com/bookmark", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(a)
    })
  }
  async function delte(r){
    let data = {
      id: r
    }
    let res = await fetch(" https://rougele.herokuapp.com/remove-book", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })

  }
  let delt = async (e) => {
    let data = {
      id: e
    }
    let res = await fetch(" https://rougele.herokuapp.com/delete-post", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    posts()

  }
  let Like = async (e) => {
    let data = {
      id: e.postid
    }



    let res = await fetch(" https://rougele.herokuapp.com/like-post", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    let h = await res.json()
  
   
    if (h.liked) {
      document.getElementById(`${e.postid}`).innerHTML = `  <div class = "flame"></div>${h.a}`

     }else{
      document.getElementById(`${e.postid}`).innerHTML = `  <div class = "lame"></div>${h.a}`
     }
 
    // setlikes([h])
    console.log(h)




  }

  
function Add() {
  let [data,setData] =  useState("");
  let [w,sw] = useState("0px")
  let token = localStorage.getItem("token")
  let width =(e)=>{
      let a = e.target.value.length * 2.51
      sw(a)

  }
 let submit = async (e) =>{
     e.preventDefault()
     let dat = e.target.data.value
     let r = new Date()
     let jo = r.toLocaleString('en-us')
     let form = {
         data:dat,
         date:jo
     }
     const res = await fetch(` https://rougele.herokuapp.com/create-post`, {
      method: "POST",
      headers: { 'Content-Type': "application/json", "Authorization":`Bearer ${token}`  },
      body:JSON.stringify(form)
  })
  let j = await res.json()
     console.log(form)
     setData("")
     posts()
    

 }


  return (
      <div>
          <form onSubmit={submit}>
              <div className="Add">
                  <textarea id="Add-Post" type="text" name="data" maxLength="155" value={data} onChange={e => { setData(e.target.value); width(e) }}></textarea>
                  <div id="border" style={{width:w}}></div>
                  <input type="submit" className="postButton" id="submitPost" value="Post" />
              </div>
          </form>

      </div>
  )

}






  useEffect(() => {
    posts()
    //  eslint-disable-next-line
  }, []);


  return (
    <div>
      <Add/>

      {load === true ?
        
        <div className="H-posts">
        {Posts.map(post => (
          <div key={post.postid} className="U-post">
            <div id="cs-12">
              <p id="data">{post.data}</p>
            </div>
          
            <div className="cs-un">
              <div id="cs-un-1"><img src={`https://matthewkwap.github.io/pic/${post.img}.jpg`} id="img4" /></div>
              <div id="cs-un-2"><a href={`/profile/${post.name}`} id="post-owner">@{post.name}</a></div>
              {/* <a href={`/${post.owner}/${post.postid}`}>mak </a> */}
              <div id="cs-un-3"><h1 id="date">Created {post.datecreated}</h1></div>
             
            </div>
            {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hellod</button>} */}
            <div class="dropdown">
              <div class="dropbtn">:</div>
              <div class="dropdown-content"> 
                {post.owner === lid ? <button  onClick={() => { delt(post.postid) }} id="drop-del">Delete</button> : <ui></ui>}
                {/* <button href="#">Follow</button> */}
                <button href="#" id="drop-bok" onClick={e=>{book(post)}}>Bookmark</button>

                {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hello</button>} */}

              </div>
            </div>
           

            {/* {post.owner === "hatice"} */}
            <div class="flex-container">
           
          

              {post.liked === true
                ?
                <div>
                  <button className={`likebtn`} id={ `${post.postid}`} onClick={a => { Like(post); }}>
                  <div class = "flame"></div>
                  </button>
                </div>
                :
                <div>
                  <button className={`likebtn`} id={ `${post.postid}`} onClick={a => { Like(post); }}>
                    <div id="blame">
                      <div class = "lame"></div>
                    </div>
                  
                  </button>
                </div>

              }  
              <Link to={`/u/${post.name}/${post.postid}`}id="c-btn">🎫Comment</Link>




            </div>



          </div>
        ))}

      </div>
        
          


        :
        <Loading />
      }



    </div>
  )


}
export default Profile