import {react,useState,useEffect} from 'react';
import { useParams, useRouteMatch } from "react-router-dom";
import Notfound from './notfound'

export default function Post(){
   
  
    const { id,user } = useParams();
    let [g, sg] = useState()
    

    let [come, setcome] = useState("");
    let [cim,sim] = useState([])
    let [img, setimg] = useState("")
    

    let [likes, setlikes] = useState();
    let token = localStorage.getItem("token")
    let lid = localStorage.getItem("name")
    const liked = "blue"
   
    const [Posts, setPosts] = useState([]);
    let posts = async () => {
      let dat = localStorage.getItem("token")
      // console.log
      let data = {
        username:user,
        id:id
      }
  
      const res = await fetch(` https://rougele.herokuapp.com/post/${data.username}/${data.id}`, {
        method: "GET",
        headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${token}` },
       
      })
  
      let j = await res.json()
  
      if (j.exist) {
      
       

        setPosts(j.data)
        sim(j.comments)
        
        setlikes(j.liked)
        setimg(j.image)

  
        sg(true)
  
  
      } else {
        sg(false)
      }
  
    }
  
    // let get = async () => {
    //   const res = await fetch(` https://rougele.herokuapp.com/user-data/${id}`, {
    //     method: "GET",
    //     headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${token}` },
    //   })
    //   let j = await res.json()
    //   setimg(j.img)
    //   setname(j.name)
    //   sun(j.uname)
    // }
    let Like = async (e) => {
      let data = {
        id: e
      }
      let res = await fetch(" https://rougele.herokuapp.com/like-post", {
        method: "POST",
        headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
        body: JSON.stringify(data)
      })
      let h = await res.json()
      
    if (h.liked) {
      document.getElementById(`${e}`).innerHTML = `  <div class = "flame"></div> <div id="like-mun">${h.a}</div>`

     }else{
      document.getElementById(`${e}`).innerHTML = `  <div class = "lame"></div> <div id="like-mun">${h.a}</div>`
     }
   
    }
    let ro = cim;

    async function coment(e){
      e.preventDefault()
      console.log(e.target.name.value )
      let r = new Date()
      let jo = `${r.getDate()}/${r.getMonth()}/${r.getFullYear()} At ${r.getHours()}:${r.getMinutes()}`
      let data = {
        id:Posts.postid,
        data:e.target.name.value,
        date:jo

      }
      let res = await fetch(" https://rougele.herokuapp.com/post-comment", {
        method: "POST",
        headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
        body: JSON.stringify(data)
      })
      let g = await res.json()
      
      ro.push(g[0])
    
      sim(g)
    }
    async function deltecomment(e,a){
      let data ={
        id:e,
        cid:a
      }
    

      let res = await fetch(" https://rougele.herokuapp.com/delete-comment", {
        method: "POST",
        headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
        body: JSON.stringify(data)
      })
      posts()

    }
    useEffect(() => {
    
      posts()
    }, []);
  
    return(
        <div>


              <div key={Posts.postid} className="ui-post">
                <div id="ui-cs-12">
                  <p id="ui-data">{Posts.data}</p>
                </div>
                <div className="ui-cs-un">
                  <div id="ui-cs-un-1"><img src={` https://rougele.herokuapp.com/${Posts.name}/photo`} id="ui-img4" /></div>
                  <div id="ui-cs-un-2"><a href={`/profile/${Posts.name}`} id="ui-post-owner">@{Posts.name}</a></div>
                  <div id="ui-cs-un-3"><h1 id="ui-date">Created {Posts.datecreated}</h1></div>
                </div>
                

                 <div className="ui-flex-container">
                  
                  {likes === true
                      ?
                      <div>
                      <button className={`likebtn`} id={ `${Posts.postid}`} onClick={a => { Like(Posts.postid); }}>
                      <div class = "flame"></div>
                      </button>
                    </div>
                    :
                    <div>
                      <button className={`likebtn`} id={ `${Posts.postid}`} onClick={a => { Like(Posts.postid); }}>
                        <div id="blame">
                          <div class = "lame"></div>
                        </div>
                      
                      </button>
                    </div>
  
                    }
  
  
              
  
                  </div>

                  <div className="ui-comments">
                    <form onSubmit={coment}>
                    <textarea type="Text" placeholder="Comment" id="ui-coment" name="name" value={come} onChange={e => setcome(e.target.value)} />
                    <input type="submit" id="comte" value="Comment"/>

                    </form>

              </div>
            </div>

            <div className="comments">
              {cim.map(data=> (
                <div className="comment">
                    <div id="cm-cs-12">
                  <p id="cm-data">{data.data}</p>
                </div>
                <div className="cm-cs-un">
                  {data.owner === lid?<button id ="daas" onClick={e=> deltecomment(data.commentid,Posts.postid)} >Delete</button>:<div></div> }
                  <div id="cm-cs-un-1"><img src={` https://rougele.herokuapp.com/${data.name}/photo`} id="cm-cs-img" /></div>
                  <div id="cm-cs-un-2"><a href={`/profile/${data.name}`} id="cm-post-owner">@{data.name}</a></div>
                  {/* <a href={`/${post.owner}/${post.postid}`}>mak </a> */}
                  <div id="cs-un-3"><h1 id="cm-date">Created {data.datecreated}</h1></div>

                </div>
                </div>
              ))}

            </div>
              

      


        </div>
    )

}