import { useParams, useRouteMatch } from "react-router-dom";
import { Link, Route, Switch } from "react-router-dom";
import { useEffect, useState } from "react";
import Notfound from './notfound'
import Loading from './loading'
import Pop from './pop'
import './like.css'
function Profile(prop) {
  const { id } = useParams();
  document.title = `@${id}`
  let [g, sg] = useState()
  let [name, setname] = useState("");
  let [img, setimg] = useState("")
  let [fol, setfol] = useState("")
  let [fli, setfli] = useState("")
  let [bio, setbio] = useState("")
  let [un, sun] = useState("")
  let [afol, safol] = useState()
  let [a,sa] = useState("none")
  let [mark,smark] = useState([])

  let [cname, scname] = useState(`${name}`)
  let [cbio, scbio] = useState(`${bio}`)

  let [gal,sga] = useState(false)
  const [selectedFile, setIsSelected] = useState();
	const [isSelected, setIsFilePicked] = useState(false);
  let [imf,simf] = useState()
  let [actd,setactd] = useState("post") 



  let [loaded, sl] = useState(false)
  let ga = true
  let [likes, setlikes] = useState([]);
  let token = localStorage.getItem("token")
  let lid = localStorage.getItem("name")
  const liked = "blue"
 
  const [Posts, setPosts] = useState([]);

 let mess = async( ) =>{
  const res = await fetch(` https://rougele.herokuapp.com/create-message`, {
    method: "Post",
    headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${token}` },
    body:JSON.stringify({username:"rakice"})
  })
  
 }
 



  let posts = async () => {
    let dat = localStorage.getItem("token")
    // console.log
    const res = await fetch(` https://rougele.herokuapp.com/user-posts/${id}`, {
      method: "GET",
      headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${token}` },

    })

    let j = await res.json()
    

    if (j.exist) {
      setPosts(j.data)
     
      setlikes(j.likes)
      sg(true)
      sl(true)

      localStorage.setItem("posts",JSON.stringify(j.data))
    } else {
      sg(false)

    }

  }

  let get = async () => {
    const res = await fetch(` https://rougele.herokuapp.com/user-data/${id}`, {
      method: "GET",
      headers: { 'Content-Type': "application/json", "Authorization": `Bearer ${token}` },
    })
    let j = await res.json()
    setimg(j.img)
    setname(j.name)
    scname(j.name)
    scbio(j.bio)
    sun(j.uname)
    setfli(j.fli)
    setfol(j.foll)
    setbio(j.bio)
    safol(j.following)
  }
  let Like = async (e) => {
    let data = {
      id: e.postid
    }



    let res = await fetch(" https://rougele.herokuapp.com/like-post", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    let h = await res.json()
  
   
    if (h.liked) {
      document.getElementById(`${e.postid}`).innerHTML = `  <div class = "flame"></div><div id="liek">${h.a}</div>`

     }else{
      document.getElementById(`${e.postid}`).innerHTML = `  <div class = "lame"></div><div id="liek">${h.a}</div>`
     }
 
    // setlikes([h])
 



  }

  let refresh = async (d) => {
    let re = await fetch(" https://rougele.herokuapp.com/post-likes", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify({ postid: d })
    })

    
    let a = await re.json() 
   
    if (a.liked) {
      document.getElementById(`${d}`).innerHTML = `  <div class = "flae"></div><div>${a.a}</div>`

     }else{
      document.getElementById(`${d}`).innerHTML = `  <div class = "lame"></div>${a.a}`
     }
    }

  let delt = async (e) => {
    let data = {
      id: e
    }
    let res = await fetch(" https://rougele.herokuapp.com/delete-post", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    posts()

  }
  let follow = async () => {
    // un 
    let data = {
      u: un
    }
    let res = await fetch(" https://rougele.herokuapp.com/follow", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
   let h = await res.json()
   if (h.a) {
    document.getElementById(`sa`).innerHTML = `    <div >following</div>`

   }else{
    document.getElementById(`sa`).innerHTML = `   <div >follow</div>`
   }



  }
  let changeprofil = async(e) =>{
    e.preventDefault()
    let han ;
    if(cname.length <= 0){
      alert("name length")
      return;
    }
    let data = {
      name:cname,
      bio:cbio
    }
    let res = await fetch(" https://rougele.herokuapp.com/change", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    handleSubmission()
    let r = await res.json()
    sga(true)
    sa("none")
    get()


  }
  let show = () =>{
    sa("block")
  }
  let close = () =>{
    sa("none")
  }
  const changeHandler = (event) => {
		setIsSelected(event.target.files[0]);
    simf(URL.createObjectURL(event.target.files[0]))
    // console.log(URL.createObjectURL(event.target.files[0]))
    setIsFilePicked(true);
	};

  const handleSubmission = async() => {
    let token = localStorage.getItem("token")
   
    const formData = new FormData();
    formData.append('File', selectedFile);
    formData.append('mile', "hello");
    
		await fetch(' https://rougele.herokuapp.com/upload',
			{
        headers: {"Authorization":`Bearer ${token}`  },
				method: 'POST',
				body: formData,
			}
		)
			.then((response) => {
        response.json()
      })
			.then((result) => {
				console.log('Success:', result);
       
			})
			.catch((error) => {
				console.error('Error:', error);
			});
   
	};
  let mes = async(r) =>{
    let data = {
      username:r
    }

    let res = await fetch(" https://rougele.herokuapp.com/create-message", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })
    let h = await res.json()
  if(h.almade){
      localStorage.setItem("messen",`${r}`)
      localStorage.setItem("chatid",`${h.id}`)
      window.location = '/messages'
    }

  }
  async function does(){
    
    let res = await fetch(" https://rougele.herokuapp.com/bookmarks", {
      method: "GET",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
    })
    let r = await res.json()

    smark(r)

  }
  function d(){
    // let g = localStorage.getItem("posts")
    // setPosts(JSON.parse(g))

    posts()
  }
  function Statep(){
    if(actd=== "book"){
      return(
        <div>
          <button id="boke" className="goh" onClick={e=>{ setactd("post");d()}} >User Posts</button>
              <button id="h-boke" onClick={e=>{ setactd("book"); does()}}>Bookmarks</button>
              {/* <button id="boke" onClick={e=>{ setactd("like")}}>Likes</button> */}
        </div>
      )
    }else if(actd === "post"){
      return(
        <div>
        <button id="h-boke" className="goh" onClick={e=>{ setactd("post");d()}} >User Posts</button>
              <button id="boke" onClick={e=>{ setactd("book");does()}}>Bookmarks</button>
              {/* <button id="boke" onClick={e=>{ setactd("like")}}>Likes</button> */}
        </div>
        )
      }else if(actd === "like"){
        return(
          <div>
           <button id="boke" className="goh" onClick={e=>{ setactd("post");d()}} >User Posts</button>
              <button id="boke" onClick={e=>{ setactd("book");does()}}>Bookmarks</button>
              {/* <button id="h-boke" onClick={e=>{ setactd("like")}}>Likes</button> */}
          </div>
        )
      }
  }
  async function book(a){
    let res = await fetch(" https://rougele.herokuapp.com/bookmark", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(a)
    })
  }
  async function delte(r){
    let data = {
      id: r
    }
    let res = await fetch(" https://rougele.herokuapp.com/remove-book", {
      method: "POST",
      headers: { 'Content-Type': 'application/json', "Authorization": `Bearer ${token}` },
      body: JSON.stringify(data)
    })

  }
   
  function Past(){
    if(actd=== "book"){
      return(
       
        <div className="U-posts">
        {mark.map(post => (
          <div key={post.postid} className="U-post">
            <div id="cs-12">
              <p id="data">{post.data}</p>
            </div>
          
            <div className="cs-un">
              <div id="cs-un-1"><img src={`https://matthewkwap.github.io/pic/${img}.jpg`} id="img4" /></div>
              <div id="cs-un-2"><a href={`/profile/${post.name}`} id="post-owner">@{post.name}</a></div>
              {/* <a href={`/${post.owner}/${post.postid}`}>mak </a> */}
              <div id="cs-un-3"><h1 id="date">Created {post.datecreated}</h1></div>
             
            </div>
            {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hellod</button>} */}
            <div class="dropdown">
              <div class="dropbtn">:</div>
              <div class="dropdown-content"> 
              <button  onClick={() => { delte(post.postid) }} id="drop-del">Remove</button>

                {/* {post.owner === lid ? <button  onClick={() => { delte(post.postid) }} id="drop-del">Remove</button> : <ui></ui>} */}
                {/* <button href="#">Follow</button> */}
                {/* <button href="#" id="drop-bok" onClick={e=>{book(post)}}>Bookmark</button> */}

                {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hello</button>} */}

              </div>
            </div>
           

            {/* {post.owner === "hatice"} */}
            <div class="flex-container">
           
          

              {post.liked === true
                ?
                <div>
                  <button className={`likebtn`} id={ `${post.postid}`} onClick={a => { Like(post); }}>
                  <div class = "flame"></div>
                  <div id="liek">
                      {mark.liked}
                    </div>
                  </button>
                </div>
                :
                <div>
                  <button className={`likebtn`} id={ `${post.postid}`} onClick={a => { Like(post); }}>
                    <div id="blame">
                      <div class = "lame"></div>
                      <div id="liek">
                      {mark.liked}
                    </div>
                    </div>
                  
                  </button>
                </div>

              }  
              <Link to={`/u/${post.name}/${post.postid}`}id="c-btn">🎫Comment</Link>




            </div>



          </div>
        ))}

      </div>
      )
    }else if(actd === "post"){
      return(
    
        <div className="U-posts">
        {Posts.map(post => (
          <div key={post.postid} className="U-post">
            <div id="cs-12">
              <p id="data">{post.data}</p>
            </div>
          
            <div className="cs-un">
              <div id="cs-un-1"><img src={`https://matthewkwap.github.io/pic/${img}.jpg`} id="img4" /></div>
              <div id="cs-un-2"><a href={`/profile/${post.name}`} id="post-owner">@{post.name}</a></div>
              {/* <a href={`/${post.owner}/${post.postid}`}>mak </a> */}
              <div id="cs-un-3"><h1 id="date">Created {post.datecreated}</h1></div>
             
            </div>
            {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hellod</button>} */}
            <div class="dropdown">
              <div class="dropbtn">:</div>
              <div class="dropdown-content"> 
                {post.owner === lid ? <button  onClick={() => { delt(post.postid) }} id="drop-del">Delete</button> : <button  >hellod</button>}
                {/* <button href="#">Follow</button> */}
                <button href="#" id="drop-bok" onClick={e=>{book(post)}}>Bookmark</button>

                {/* {post.owner === lid ? <button id="sa" onClick={() => { delt(post.postid) }}>hello</button> : <button id="sa" onClick={() => { delt(post.postid) }}>hello</button>} */}

              </div>
            </div>
           

            {/* {post.owner === "hatice"} */}
            <div class="flex-container">
           
          

              {post.liked === true
                ?
                <div>
                  <button className={`likebtn`} id={ `${post.postid}`} onClick={a => { Like(post); }}>
                  <div class = "flame"></div>
                  <div id="liek">
                      {post.likes}
                    </div>
                  </button>
                </div>
                :
                <div>
                  <button className={`likebtn`} id={ `${post.postid}`} onClick={a => { Like(post); }}>
                    <div id="blame">
                      <div className = "lame"></div>
                    </div>
                    <div id="liek">
                      {post.likes}
                    </div>
                  
                  </button>
                </div>

              }  
              <Link to={`/u/${post.name}/${post.postid}`}id="c-btn">🎫Comment</Link>




            </div>



          </div>
        ))}

      </div>
        )
      }else if(actd === "like"){
        return(
          <div>
           <button id="boke" className="goh" onClick={e=>{ setactd("post");d()}} >User Posts</button>
              <button id="boke" onClick={e=>{ setactd("book");does()}}>Bookmarks</button>
              <button id="h-boke" onClick={e=>{ setactd("like")}}>Likes</button>
          </div>
        )
      }

  }
  



  useEffect(() => {
    get()
    posts()
  }, []);





  return (
    <div>
      {gal === true? <Pop name="Profile Change"/>:<div></div>}
      
     

      <div className="ed" style={{display:a}}>
        <div className="ed-p">
          <form onSubmit={changeprofil}>
            <div id="ed-bar">
            <div id="hello1" onClick={close}>
              <svg viewBox="0 0 24 24" aria-hidden="true" id="hello"><g><path d="M13.414 12l5.793-5.793c.39-.39.39-1.023 0-1.414s-1.023-.39-1.414 0L12 10.586 6.207 4.793c-.39-.39-1.023-.39-1.414 0s-.39 1.023 0 1.414L10.586 12l-5.793 5.793c-.39.39-.39 1.023 0 1.414.195.195.45.293.707.293s.512-.098.707-.293L12 13.414l5.793 5.793c.195.195.45.293.707.293s.512-.098.707-.293c.39-.39.39-1.023 0-1.414L13.414 12z"></path></g></svg>
            </div>
            <input type="submit" id="hello2" value="Save"/>
          </div>
          {/* <div id="ed-img">
            <h1 id="ed-pic">Change Your Profile Picture</h1>
          
          
           {/* <input type="file" name="file" id="change12"  onChange={changeHandler} /> 
        		{isSelected ? (
				<div>
          <img src={imf} id="selected-imag" />
          </div>
			) : (
           <div> <input type="file" name="file" id="ed-imag"   onChange={changeHandler} /></div>				
			)}
            

          </div> */}
          <label id="ed-n">Name</label>
          <input name="name" value={cname} onChange={e => scname(e.target.value)} id="ed-name" />
          <label id="ed-b">Bio</label>
          <textarea name="bio" value={cbio} maxLength="155" onChange={e => scbio(e.target.value)} id="ed-bio" />

          </form>
          
        </div>
      </div>




      {g === true ?
        <div>
          <div className="User-info">
            <div id="img">
              <img src={`https://matthewkwap.github.io/pic/${img}.jpg`} id="img3" />
              
            </div>
            
            {un === lid ? <button id="sa" onClick={show}>Edit Profile</button> : <button id="sa" onClick={follow}>
              {afol === true ?
                <div >following</div>
                :
                <div >follow</div>
              }

            </button>}
            {/* {un===lid?
            <div></div> :
             <button  id="ba" onClick={e=>mes(un)}>message</button>
          } */}
           


            <h1 id="User-name">{name}</h1>
            <h1 id="u-u-n">@{un}</h1>
            <div id="User-bio">
              <h1 id="u-bio">{bio}</h1>
            </div>
            <div>
              <label id="fol">
  
  
                <h1>{fol} following</h1>
                <h1>{fli} followers</h1>

              </label>
            </div>
            <div className="prof-nav">
            <Statep/>
            </div>
          </div>
          <Past/>

        </div>
        :
        <Notfound />
      }



    </div>
  )


}
export default Profile